.design{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/bg.png");
    background-size:cover;
    background-position:center center;
    height:1900px;
    width:100%;
    padding-top:64px;
    .container{
    }
  }
  .df{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }
  .dfc{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-end
  }
  .df-end{
    display: flex;
    justify-content: flex-end;
  }
  .design .title{
    font-size:48px;
    line-height:72px;
    color:white;
    text-align:center;
    margin-bottom:63px;
  }
  .design-box{
    display:flex;
    justify-content:flex-start;
    
  }
  .half{
    height:50%;
  }
  .design-end{
    width:auto;
    display: flex;
    justify-content: flex-end;
  }
  .container{
    margin-right:100px;
    margin-left:100px;
  }
  .join1{
    margin-bottom: 60px;
  }
  .join1-left{
    margin-right: 8px;
  }
  .join1row-left{
    margin-right:8px;
  }
  .join1-right-top{
    margin-bottom: 8px;
  }
  .join1row-pic1-pb{
    padding-bottom: 8px;
  }

  .pic4{
    max-width:100%;
    height:100%;
    margin-right: 8px;
  }
  .pic5{
    max-width:100%;
    height:100%;
  }
  .pic6{
    max-width:100%;
    height:100%;
  }
  .pic7{
    max-width:100%;
    height:100%;
  }
  .pic8{
    max-width:100%;
    height: 100%;
  }
  .pdr{
    padding-right:8px
  }

  .join2{
    margin-bottom:144px;
    display:flex;
    flex-direction:row;
    justify-content:center;
  }
  .join2inside{
    display:flex;
    justify-content:center;
    align-items:center;
    height:592px;
    width:592px;
  }
  .join2inside::before{
    background-color:white;
    width:100%;
    height:100%;
    position: absolute;
    top:0;
    left:0;
    opacity:0.5;
  }
  .join2inside .content{
    color:#AA0601;
    font-size:72px;
    line-height:108px;
  }
  .join2pic1{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/product-8.png");
    background-size:cover;
    background-position:center center;
    margin-right: 30px;
  }
  .join2pic2{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/product-9.png");
    background-size:cover;
    background-position:center center;
  }
  .customerbox{
    width:100%;
    margin-bottom:80px;
    margin-top: -250px;
    .title{
      color:white;
      text-align:center;
      font-size:48px;
      line-height:72px;
      padding: 10px;
      margin-bottom: 60px;
    }
  } 
  .quote{
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .quotebox{
    max-width:282px;
    padding-left:12px;
    padding-right:12px;
  }
  .quotepic{
    width:100%;
    height:256px;
    background-size:cover;
    background-position:center center;
  }
  .rec1{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/rec-1.png")
  }
  .rec2{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/rec-2.png")
  }
  .rec3{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/rec-3.png")
  }
  .rec4{
    background-image: url("https://hexschool.github.io/webLayoutTraining1st/week3/rec-4.png")
  }
  .quotebox-inner{
    font-size:16px;
    line-height: 24px;
    padding:16px;
    border:1px solid #DEE2E6;
  }
  .quote-margin{
    margin-bottom:16px;
  }
@media(max-width:992px){
  .quote{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap:wrap;
  }
  .quotebox{
    max-width:324px;
    height:auto;
    padding-left:12px;
    padding-right:12px;
    margin-bottom:24px;
  }
  .pic4{
    height:auto;
  }
  .pic5{
    height:auto;
  }
  .pic6{
    height:auto;
  }
  .pic7{
    height:auto;
  }
  .pic8{
    height:auto;
  }

}
@media(max-width:767px){

  .join2{
    margin-bottom:144px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    padding-left:11px;
    padding-right: 12px;
    padding-bottom: 24px;
  }
  .join2inside{
    display:flex;
    justify-content:center;
    align-items:center;
    height:394px;
    width:auto;
  }
  .quote{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap:nowrap;
  }
  .quotebox{
    width:auto;
    min-width:352px;
    height:auto;
    padding-left:12px;
    padding-right:12px;
    margin-bottom:16px;
  }

}

